@import "../../global.scss";

.portfolio {
    background-color: lightgreen;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        font-size: 50px;

        @include mobile { 
            font-size: 20px;
        }
    }

    ul {
        padding: 0;
        margin: 10px;
        list-style: none;
        display: flex;

        @include mobile {
            // font-size: 20px;
            flex-wrap: wrap;
            justify-content: center;
            margin: 10 0;
        }

    }

    .container {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;

        flex-wrap: wrap;

        @include mobile {
            width: 100%;

        }

        .item {
            //COME BACK AND FIX THE IMAGE SIZING HERE
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px soild gray;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: rgb(217, 165, 42);
            position: relative;
            transition: all 1s ease;
            cursor: pointer;

            @include mobile {
                width: 130px;
                height: 100px;
            }

            h3 {
                position: absolute;
                font-size: 20px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover {
                background-color: $mainColor;
                img {
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}